import React from "react";
import { storiesOf } from "@storybook/react"

import Datepicker from "../components/Datepicker";

const stories = storiesOf("Example", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add("Ex1", () => {
  const date = new Date()
    return (
        <div>
          <h1>React Datepicker</h1>
          <h2>By default</h2>
          <Datepicker
            date = {date}
          />
        </div>
    )
});

stories.add("Ex2", () => {
  const date = new Date()

  const customDatePicker = {
    borderRadius: "5px",
    border: "none",
    backgroundColor: "rgba(240, 67, 41, .3)",
    fontFamily: "Roboto",
    fontSize: "15px",
  }

  const customSelectedDay = {
    backgroundColor: "#594B94"
  }

  const customHeader = {
    backgroundColor: "rgba(240, 67, 41, .6)",
  }

  return (
    <div>
    <h1>React Datepicker</h1>
    <h2>Customized</h2>
    <Datepicker
      date = {date}
      styleDatePicker = {customDatePicker}
      styleSelectedDay = {customSelectedDay}
      styleHeader = {customHeader}
    />
    </div>
  )
});